@use "../util/colors" as c;

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    border-bottom: 2px solid c.$black;

    h1 {
        margin: 1.5rem 3vw;
        font-size: 3rem;
    }

    @media only screen and (max-width: 450px) {
        h1 {
            font-size: 2rem;
        }
    }

    & input,
    & button {
        margin-right: 2vw;
        height: 32px;
    }

    button {
        width: fit-content;
        padding: 0 .5rem;
        max-width: 140px;

        .material-symbols-outlined {
            font-variation-settings:
                'FILL' 0,
                'wght' 700,
                'GRAD' 0,
                'opsz' 48
        }
    }

    input {
        min-width: 140px;

        width: 20vw;
        font-size: 1.5rem;
    }
}