.modal {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #00000080;
    display: flex;
    align-items: center;
    justify-content: center;

    .modal-content {
        width: 500px;
        background-color: #fff;
    }

    .modal-header,
    .modal-footer {
        padding: 10px;
    }

    .modal-title {
        margin: 0;
    }

    .modal-body {
        padding: 10px;
        margin: auto;
        width: fit-content;
        border-top: 1px solid #eee;
        border-bottom: 1px solid #eee;
    }
}